import React, { useReducer } from "react";
import { scaleLinear } from "d3-scale";
import useResizeObserver from "use-resize-observer";
import useSolarized from "./useSolarized";
import ModeButton from "./ModeButton";
import Toggle from "./Toggle";
import "./App.css";
import data from "./data";

const darkModeStart = window.matchMedia("(prefers-color-scheme: dark)").matches;

const dayToBars = (day, scaleY, theme, options) => {
  const count = Object.values(options).reduce((prev, curr) => prev + curr, 0);
  const size = 6 / count;
  if (count === 0) {
    return <></>;
  }
  const data = [];
  if (options.easy) {
    console.log({ day });
    const easy = day.easy
      .reduce((prev, curr) => [...prev, prev[prev.length - 1] + curr], [0])
      .map((v) => ({ v, color: theme.green }));
    data.push(easy);
  }
  if (options.medium) {
    const medium = day.medium
      .reduce((prev, curr) => [...prev, prev[prev.length - 1] + curr], [0])
      .map((v) => ({ v, color: theme.blue }));
    data.push(medium);
  }
  if (options.hard) {
    const hard = day.hard
      .reduce((prev, curr) => [...prev, prev[prev.length - 1] + curr], [0])
      .map((v) => ({ v, color: theme.red }));
    data.push(hard);
  }
  return (
    <>
      {data.map((d, i) => {
        return d
          .reverse()
          .map(({ v, color }) => (
            <rect
              key={v}
              width={size}
              y={scaleY(v)}
              x={size * i}
              height={90 - scaleY(v)}
              fill={color}
              strokeWidth="0.25"
              stroke={theme.background}
            />
          ));
      })}
    </>
  );
};

const weekToDays = (week, scaleY, theme, options) => {
  return (
    <>
      {week.days.map((day, i) => (
        <g
          key={JSON.stringify(day)}
          style={{ transform: `translateX(${i * 8}px)` }}
          className="week"
        >
          {dayToBars(day, scaleY, theme, options)}
          <text
            style={{ fontSize: 2 }}
            x="3"
            y={scaleY(-10)}
            textAnchor="middle"
            fill={theme.primary}
          >
            Day {i + 1}
          </text>
        </g>
      ))}
      <text
        style={{ fontSize: 4 }}
        x="11"
        y={scaleY(-24)}
        textAnchor="middle"
        fill={theme.primary}
      >
        Week {week.index}
      </text>
    </>
  );
};

const optionsReducer = (state, action) => {
  switch (action.type) {
    case "easy": {
      return { ...state, easy: !state.easy };
    }
    case "medium": {
      return { ...state, medium: !state.medium };
    }
    case "hard": {
      return { ...state, hard: !state.hard };
    }
    default:
      return state;
  }
};

function App() {
  const [options, dispatch] = useReducer(optionsReducer, {
    easy: true,
    medium: true,
    hard: true,
  });
  const [theme, lightMode, setLightMode] = useSolarized(!darkModeStart);
  const scaleY = scaleLinear().domain([0, 260]).range([90, 10]);
  const { ref, width } = useResizeObserver();
  const canvasWidth = width < 1240 ? width - 40 : 1200;
  const canvasHeight = ~~(canvasWidth / 2);

  return (
    <div
      ref={ref}
      className="App"
      style={{
        color: theme.primary,
        backgroundColor: theme.background,
        transition: "background-color 0.5s, color 0.25s",
      }}
    >
      <h2 style={{ textAlign: "center" }}>
        Visualization of the{" "}
        <a
          style={{ color: theme[8], transition: "color: 0.5s" }}
          href="https://hundredpushups.com/"
        >
          One Hundred Pushups
        </a>
        -program
      </h2>
      <svg
        width={canvasWidth}
        height={canvasHeight}
        viewBox="0 0 200 100"
        className="graph"
      >
        {[0, 50, 100, 150, 200, 250].map((n) => (
          <g key={n} transform={`translate(0 ${scaleY(n)})`}>
            <text
              style={{ fontSize: 4, textAnchor: "end" }}
              x="9"
              y="1"
              fill={theme.primary}
            >
              {n}
            </text>
            <line
              x1="10"
              x2="190"
              y1={0}
              y2={0}
              stroke={theme.primary}
              strokeWidth="0.25"
              style={{ opacity: 0.5 }}
            />
          </g>
        ))}
        <g style={{ transform: "translateX(14px)" }}>
          {data.map((week) => (
            <g
              key={week.index}
              style={{ transform: `translateX(${(week.index - 1) * 26}px)` }}
            >
              {weekToDays(week, scaleY, theme, options)}
            </g>
          ))}
          <g style={{ transform: `translateX(${6 * 26}px)` }}>
            <g style={{ transform: `translateX(${8}px)` }} className="week">
              {dayToBars(
                {
                  easy: [100],
                  medium: [100],
                  hard: [100],
                },
                scaleY,
                theme,
                options
              )}
            </g>
            <text
              style={{ fontSize: 2 }}
              x="11"
              y={scaleY(-10)}
              textAnchor="middle"
              fill={theme.primary}
            >
              Goal
            </text>
            <text
              style={{ fontSize: 4 }}
              x="11"
              y={scaleY(-24)}
              textAnchor="middle"
              fill={theme.primary}
            >
              Week 7
            </text>
          </g>
        </g>
      </svg>
      <div>
        <br />
        <div
          style={{
            display: "grid",
            gridTemplateColumns: "auto auto 10px auto auto 10px auto auto",
            columnGap: 6,
            rowGap: 2,
          }}
        >
          <span style={{ justifySelf: "end", color: theme.green }}>Easy</span>
          <Toggle
            theme={theme}
            on={options.easy}
            onClick={() => dispatch({ type: "easy" })}
          />
          <div />
          <span style={{ justifySelf: "end", color: theme.blue }}>Medium</span>
          <Toggle
            theme={theme}
            on={options.medium}
            onClick={() => dispatch({ type: "medium" })}
          />
          <div />
          <span style={{ justifySelf: "end", color: theme.red }}>Hard</span>
          <Toggle
            theme={theme}
            on={options.hard}
            onClick={() => dispatch({ type: "hard" })}
          />
        </div>
      </div>
      <ModeButton theme={theme} onClick={() => setLightMode(!lightMode)} />
    </div>
  );
}

export default App;
